/* src/HomeSection.css */
.home {
    height: 100vh;
    background: url('backcover.png') no-repeat;
    background-size: cover;
    background-position: center;
    display: flex;
    align-items: center;
    padding: 0 10%;
  }
  
  .home-content {
    max-width: 600px;
  }
  
  .home-content h1 {
    position: relative;
    font-size: 56px;
    font-weight: 700;
    line-height: 1.2;
  }
  
  .home-content h1::before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background: #081b29;
    animation: showRight 1s ease forwards;
    animation-delay: .1s;
  }
  
  .home-content h3 {
    position: relative;
    font-size: 32px;
    font-weight: 700;
    color: #5E5E5E;
  }
  
  .home-content h3::before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background: #081b29;
    animation: showRight 1s ease forwards;
    animation-delay: 1.3s;
  }
  
  .home-content p {
    position: relative;
    font-size: 16px;
    margin: 20px 0 40px;
  }
  
  .home-content p::before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background: #081b29;
    animation: showRight 1s ease forwards;
    animation-delay: 1.6s;
  }
  
  .home-content .btn-box {
    position: relative;
    display: flex;
    justify-content: space-between;
    width: 345px;
    height: 50px;
  }
  
  .home-content .btn-box::before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background: #081b29;
    animation: showRight 1s ease forwards;
    animation-delay: 1.5s;
    z-index: 2;
  }
  
  .btn-box a {
    position: relative;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 150px;
    height: 100%;
    background: #5E5E5E;
    border: 2px solid #5E5E5E;
    border-radius: 8px;
    font-size: 19px;
    color: #081b29;
    text-decoration: none;
    font-weight: 600;
    letter-spacing: 1px;
    z-index: 1;
    overflow: hidden;
    transition: .5s;
  }
  
  .btn-box a:hover {
    color: #00abf0;
  }
  
  .btn-box a:nth-child(2) {
    background: transparent;
    color: #00abf0;
  }
  
  .btn-box a:nth-child(2):hover {
    color: #5d7383;
  }
  
  .btn-box a:nth-child(2)::before {
    background: #00abf0;
  }
  
  .btn-box a::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 0;
    height: 100%;
    background: #081b29;
    z-index: -1;
    transition: .5s;
  }
  
  .btn-box a:hover::before {
    width: 100%;
  }
  
  .home-sci {
    position: absolute;
    bottom: 50px;
    width: 180px;
    display: flex;
    justify-content: space-between;
  }
  
  .home-sci::before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background: #081b29;
    animation: showRight 1s ease forwards;
    animation-delay: 2.5s;
    z-index: 2;
  }
  
  .home-sci a {
    position: relative;
    display: inline-flex;
    justify-content: center;
    align-content: center;
    width: 40px;
    height: 40px;
    background: transparent;
    border: 2px solid #00abf0;
    border-radius: 50%;
    font-size: 20px;
    color: #00abf0;
    text-decoration: none;
    z-index: 1;
    overflow: hidden;
    transition: .5s;
  }
  
  .home-sci a:hover {
    color: #081b29;
  }
  
  .home-sci a::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 0;
    height: 100%;
    background: #00abf0;
    z-index: -1;
    transition: .5s;
  }
  
  .home-sci a:hover::before {
    width: 100%;
  }
  
  .home-imageHover {
    position: absolute;
    top: 0;
    right: 30px;
    width: 500px;
    height: 100%;
    background: transparent;
    transition: 3s;
    animation: manipActiveHover .1s forwards;
    animation-delay: 4s;
    pointer-events: none;
  }
  
  .home-imageHover:hover {
    background: #081b29;
    opacity: .8;
  }
  
  .home-imageHover::after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    width: 120%;
    height: 100%;
    background: #081b29;
    animation: showRight 1s ease forwards;
    animation-delay: 3s;
    z-index: 100;
  }
  
  @keyframes showRight {
    100% {
      width: 0;
    }
  }
  
  @keyframes manipActiveHover {
    100% {
      pointer-events: auto;
    }
  }
  