/* src/Carousel.css */

.carousel-container {
    width: 100%;
    overflow: hidden;
    white-space: nowrap;
    position: relative;
    top: 1px;
    background-color: #3b3b3b;
    
  }
  
  .carousel-container:hover .carousel-slide {
    animation-play-state: paused;
  }
  
  .carousel-slide {
    height: 100%;
    animation: 25s slide infinite linear;
    display: inline-flex; /* Use inline-flex for horizontal layout */
    transition: transform 0.5s ease-in-out;
  }
  
  .carousel-slide img {
    height: 300px;
    object-fit: cover;
    margin-right: 5px; /* Add margin between images if needed */
  }
  
  @keyframes slide {
    from {
      transform: translateX(0);
    }
    to {
      transform: translateX(-100%);
    }
  }
  